import React from "react";
import s from "./CardInMyBookmarks.module.scss";
import { Link } from "react-router-dom";
import noImg from "../../images/noImg.jpg";

export default function CardInMyBookmarks({ movies, deleteFilm }) {
  return (
    <>
      {movies.length > 0 ? (
        movies
          .filter((el) => el[0].description.length > 0 && el[0].name.length > 0)
          .map((el, index) => (
            <div className={s.card} key={index}>
              <div className={s.card__img}>
                <Link to={"/movie/" + el[0].id}>
                  {el[0].poster === null ? <img src={noImg} alt="картинка" /> : <img src={el[0].poster} alt="картинка" />}
                </Link>
              </div>
              <div className={s.card__info}>
                <div className={s.card__wrapper}>
                  <Link to={"/movie/" + el[0].id}>
                    <h2 className={s.card__name}>{el[0].name}</h2>
                    <p className={s.card__description}>{el[0].description}</p>
                    <div className={s.card__year}>{el[0].year} г.</div>
                  </Link>
                  <button
                    className={s.card__btn}
                    onClick={() => {
                      deleteFilm(el[1], el[0].id);
                    }}>
                    X
                  </button>
                </div>
              </div>
            </div>
          ))
      ) : (
        <p className={s.card__noFilms}>У вас нет добавленных фильмов...</p>
      )}
    </>
  );
}
